import React, { useState, useEffect, useRef } from 'react';

import Blog from './components/Blog';
import Footer from './components/Footer';
import Notification from './components/Notification';
import BlogForm from './components/BlogForm';
import Togglable from './components/Togglable';
import LoginForm from './components/LoginForm';

import blogService from './services/blogs';
import loginService from './services/login';

const App = (props) => {
  const [blogs, setBlogs] = useState(props.blogs);
  const [showAll, setShowAll] = useState(true);
  const [notificationMessage, setNotificationMessage] = useState('');
  const [notificationType, setNotificationType] = useState('');
  const [user, setUser] = useState(null);

  const blogFormRef = useRef();

  useEffect(() => {
    const loggedUserJSON = window.localStorage.getItem('loggedBlogAppUser');

    if (loggedUserJSON) {
      const user = JSON.parse(loggedUserJSON);

      setUser(user);
      blogService.setToken(user.token);
    }
  }, []);

  useEffect(async () => {
    try {
      const blogs = await blogService.getAll();
      setBlogs(blogs);
    } catch (e) {
      notify(`Blog viewing failed with exception: ${e}`, 'error');
    }
  }, []);

  const blogForm = () => (
    <Togglable buttonLabel="new blog" ref={blogFormRef}>
      <BlogForm createBlog={addBlog} />
    </Togglable>
  );

  const addBlog = async (blogObject) => {
    blogFormRef.current.toggleVisibility();

    try {
      const returnedBlog = await blogService.create(blogObject);

      setBlogs(blogs.concat(returnedBlog));

      notify(`Blog added with success`, 'success');
    } catch (exception) {
      notify(`Blog addition failed with exception: ${exception}`, 'error');
    }
  };

  const updateBlog = async (newObject) => {
    console.log(`APP.JS button clicked and likes ${newObject.likes}`);

    try {
      const returnedBlog = await blogService.update(newObject.id, newObject);

      blogs.filter((x) => x.id === newObject.id).likes++;
      setBlogs(blogs);

      notify(`Good choice`, 'success');
    } catch (exception) {
      notify(`Couldn't increase the likes: ${exception}`, 'error');
    }
  };

  const handleLogin = async (userObject) => {
    const username = userObject.username;
    const password = userObject.password;

    try {
      const user = await loginService.login({
        username,
        password,
      });

      window.localStorage.setItem('loggedBlogAppUser', JSON.stringify(user));

      blogService.setToken(user.token);
      setUser(user);

      notify(`Welcome in you dear ${user.name}`, 'success');
    } catch (exception) {
      notify(`wrong user name or password`, 'error');
    }
  };

  const loginForm = () => {
    return (
      <Togglable buttonLabel="login">
        <LoginForm handleSubmit={handleLogin} />
      </Togglable>
    );
  };

  const logout = () => {
    window.localStorage.removeItem('loggedBlogAppUser');

    notify(`Bye, ${user.name}`, 'success');

    blogService.setToken(null);
    setUser(null);
    loginForm();
  };

  const notify = (notificationMessage, notificationType) => {
    setNotificationMessage(notificationMessage);
    setNotificationType(notificationType);

    setTimeout(() => {
      setNotificationMessage('');
      setNotificationType('');
    }, 5000);
  };

  return (
    <div>
      <h1>Blogs</h1>
      <Notification message={notificationMessage} type={notificationType} />

      {user === null ? (
        loginForm()
      ) : (
        <div>
          <p>
            {user.name} logged in
            <button onClick={() => logout()}> logout </button>
          </p>
          {blogForm()}
        </div>
      )}

      <ul>
        {blogs.map((blog, i) => (
          <Blog key={i} blog={blog} updateLikes={updateBlog} />
        ))}
      </ul>

      <Footer />
    </div>
  );
};

export default App;
